@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body,
html {
  font-family: "Poppins", Helvetica, sans-serif;
  min-height: 100vh;
}

* {
  margin: 0;
  padding: 0;
}

.header {
  height: 100vh;
  width: 100vw;
}

.header__avatar {
  cursor: pointer;
  position: absolute !important;
  right: 20px;
  top: 20px;
}

.MuiPopover-paper {
  max-height: calc(100vh - 62px - 100px);
  width: 354px;
  margin-top: 20px;
  margin-right: -10px;
  height: 390px;
}

.home__popoverContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
}

.home__popover__top {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #dadce0;
  width: 120%;
  height: 190px;
}

.home__badge {
  background: #fff;
  color: #5f6368;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.home__Camera {
  width: 17px !important;
  height: 17px !important;
  margin: 4px;
  box-shadow: 0px 1px 1px 0 rgba(65, 69, 73, 0.3),
    0px 1px 3px 1px rgba(65, 69, 73, 0.15);
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
}

.home__Camera:hover {
  color: #6ba4f1;
}

.home__text {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 15px;
  color: #202124;
}

.home__displayName {
  font-family: "Poppins", sans-serif;
  color: #202124;
  font-weight: 500;
  letter-spacing: -0.2px;
}

.home__mail {
  font-family: "Roboto", sans-serif;
  color: #5f6368;
}

.home__btn {
  background-color: #ffffff;
  border: 1px solid #dadce0;
  border-radius: 100px;
  color: #3c4043;
  margin: 16px 0 0;
  max-width: 254px;
  padding: 8px 16px;
  font-family: "Poppins";
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 15px;
  font-family: "Poppins";
}

.home__btn:hover {
  background-color: #f7f8f8;
}

.home__popover__btm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.home__addBtn {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 354px;
  border-bottom: 1px solid #dadce0;
  color: #5f6368;
  cursor: pointer;
  font-family: "Poppins";
  padding: 10px;
}

.home__addBtn:hover {
  background-color: #f7f8f8;
}

.home__addIcon {
  margin-left: 40px;
  margin-right: 20px;
}

.home__signOut {
  margin-top: 20px !important;
  color: #5f6368 !important;
  text-transform: capitalize !important;
}

.home__popover__footer {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  bottom: 0;
  width: 120%;
  -webkit-align-items: center;
          align-items: center;
  color: #5f6368;
  -webkit-justify-content: center;
          justify-content: center;
  border-top: 1px solid #dadce0;
  padding-top: 5px;
}

.home__popover__footer  span {
  margin: 10px;
}
.signup__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.signup {
  width: 750px;
  border: 1px solid #dadce0;
  border-radius: 7px;
  padding: 40px;
  height: 80vh;
  position: relative;
}

.signup__figure {
  text-align: center;
  margin-right: 0;
}

.signup__figureImg {
  max-width: 100%;
  width: 244px;
  height: 244px;
}

.signin__error{
  color: #df3a64 !important;
}

.signup__figcaption {
  font-size: 16px;
  font-weight: 300;
}

.signup__heading {
  font-size: 24px;
  font-weight: 400;
  line-height: 1.3;
}

.signup__subheading {
  font-size: 16px;
  font-weight: 400;
  margin-top: 5px;
}

.signup__inputs {
  margin-top: 40px;
}

.signup__nameInputs {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 20px;
}

.signup__nameInput {
  margin-right: 10px !important;
}

.signup__passwordInputs {
  margin-top: 10px;
  width: 100%;
}

.signup__passwordWrapper {
  display: -webkit-flex;
  display: flex;
}

.signup__passwordInput {
  margin-right: 10px !important;
}

.signup__helperText {
  color: rgba(0, 0, 0, 0.54);
  font-size: 0.75rem;
  margin-top: 3px;
  font-weight: 400;
  margin-left: 10px;
}

.signup__buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 40px;
}

.signup__button {
  font-weight: 500;
  text-transform: capitalize;
}

.signup__loading {
  position: absolute !important;
  top: 30px;
  left: 10px;
  width: 100% !important;
}

@media (max-width: 665px) {
  .signup {
    width: 80vw;
    border: none;
  }

  .signup__nameInputs {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .signup__nameInput {
    margin-bottom: 20px !important;
  }
  .signup__loading {
    top: -1px;
    left: -30px;
    right: 0;
  }
}

@media (max-width: 850px) {
  .signup {
    padding: 40px;
    width: 50vw;
  }
  .signup__container {
    -webkit-flex-direction: row;
            flex-direction: row;
    position: relative;
  }

  .signup__figure {
    display: none;
  }
}
.login {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.login__content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid #dadce0;
  border-radius: 7px;
  padding: 30px;
  height: 520px;
  width: 440px;
  position: absolute;
}

.login__logo {
  width: 100px;
}

.login__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.login__title {
  font-size: 24px;
  font-weight: 400;
  margin-top: 5px;
}

.login__subtitle {
  font-size: 16px;
  font-weight: 400;
  margin-top: 5px;
}

.login__form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.login__input {
  margin-top: 20px !important;
}

.login__infoText {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 50px;
  color: #5f6368;
  font-size: 14px;
}

.login__infoText a {
  color: #1a73e8;
  font-weight: 500;
  text-decoration: none;
}

.login__buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 40px 0 0;
}

.login__button {
  font-weight: 500 !important;
  text-transform: capitalize;
}

.login__button:first-of-type {
  padding-left: 0;
}

@media (max-width: 600px) {
  .login__content {
    border: none;
    width: 85vw;
    position: absolute;
    top: 0;
    margin: 0;
  }
}

.login__loading{
  -webkit-animation: a-s 500ms 500ms 1 forwards;
          animation: a-s 500ms 500ms 1 forwards;
  background-color: #f1f1f1;
  height: 4px;
  width: 114%;
  margin: -30px -10px 30px -10px;
  opacity: 0;
  overflow: hidden;
  position: relative;
}

.login__loading::before{
  -webkit-animation: a-lb 20s 500ms linear forwards;
          animation: a-lb 20s 500ms linear forwards;
  background-color: #3684eb;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  -webkit-transform: translate(-300px);
          transform: translate(-300px);
  width: 100%;
}

@-webkit-keyframes a-s {
  100% {
    opacity: 1;
  }
}

@keyframes a-s {
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes a-lb {
  0% {
    -webkit-transform: translate(-300px);
            transform: translate(-300px);
  }
  5% {
    -webkit-transform: translate(-240px);
            transform: translate(-240px);
  }
  15% {
    -webkit-transform: translate(-30px);
            transform: translate(-30px);
  }
  25% {
    -webkit-transform: translate(-30px);
            transform: translate(-30px);
  }
  30% {
    -webkit-transform: translate(-20px);
            transform: translate(-20px);
  }
  45% {
    -webkit-transform: translate(-20px);
            transform: translate(-20px);
  }
  50% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px);
  }
  65% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px);
  }
  70% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }
  95% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }
  100% {
    -webkit-transform: translate(-5px);
            transform: translate(-5px);
  }
}

@keyframes a-lb {
  0% {
    -webkit-transform: translate(-300px);
            transform: translate(-300px);
  }
  5% {
    -webkit-transform: translate(-240px);
            transform: translate(-240px);
  }
  15% {
    -webkit-transform: translate(-30px);
            transform: translate(-30px);
  }
  25% {
    -webkit-transform: translate(-30px);
            transform: translate(-30px);
  }
  30% {
    -webkit-transform: translate(-20px);
            transform: translate(-20px);
  }
  45% {
    -webkit-transform: translate(-20px);
            transform: translate(-20px);
  }
  50% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px);
  }
  65% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px);
  }
  70% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }
  95% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }
  100% {
    -webkit-transform: translate(-5px);
            transform: translate(-5px);
  }
}

